.input-button {
    position: absolute;
    right: 0;
    bottom: 0;
    margin: -10px;
    border: unset;
    border: 4px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    font-size: 30px;
    font-weight: bold;
    background-color: rgb(255, 166, 0);
    cursor: pointer;

		-webkit-tap-highlight-color: transparent;
}

@media (max-width: 540px) {
  .button-container {
      display: flex;
      justify-content: center;
      align-items: center;
  }

  .input-button {
      position: static;
      margin-top: 20px;
  }
}