.themeMode-icon {
	position: absolute;
	right: 90px;
	top: 22px;
}

.switch {
  position: absolute;
	right: 20px;
	top: 20px;
  display: inline-block;
  width: 60px;
  height: 34px; 
	-webkit-tap-highlight-color: transparent;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
	-webkit-tap-highlight-color: transparent;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

@media (max-width: 505px) {

	.themeMode-icon {
		width: 20px;
		right: 65px;
	}

	.switch {
		right: 0px;
	}

	.slider.round {
    width: 38px;
    height: 23px;
	}

	.slider.round:before {
		height: 19px;
    width: 19px;
    left: 2px;
    bottom: 2px;
	}

	input:checked + .slider:before {
		-webkit-transform: translateX(15px);
		-ms-transform: translateX(15px);
		transform: translateX(15px);
	}
}